<template>
  <div class="post" :style="`background: ${bg}; color: ${text}`">
    <svg
      version="1.1"
      id="Layer_1"
      xmlns="http://www.w3.org/2000/svg"
      xmlns:xlink="http://www.w3.org/1999/xlink"
      x="0px"
      y="0px"
      viewBox="0 0 162.9 186.6"
      style="enable-background: new 0 0 162.9 186.6"
      xml:space="preserve"
    >
      <path
        :fill="text"
        d="M160.2,51.1l0.1,85.4c0,1.4-0.7,2.7-1.9,3.4l-73.9,42.8c-1.2,0.7-2.7,0.7-3.9,0L6.5,140c-1.2-0.7-1.9-2-1.9-3.4L4.5,51.3
	c0-1.4,0.7-2.7,1.9-3.3L80.3,5.1c1.2-0.7,2.7-0.7,3.9,0l74,42.6C159.4,48.4,160.2,49.7,160.2,51.1z"
      />
      <path
        class="st0"
        :fill="bg"
        d="M116.1,130.9l12.1-6.9c0.8-0.5,1.3-1.3,1.3-2.3V64.8c0-0.9-0.5-1.8-1.3-2.3l-9.8-5.7c-0.8-0.5-1.9-0.5-2.7,0
	L82.4,77.4v22.1l25.8-16.8c1.7-1.1,4,0.1,4,2.2l0,43.8C112.2,130.6,114.3,131.9,116.1,130.9z"
      />
      <path
        class="st0"
        :fill="bg"
        d="M52.6,128.6l0-43.8c0-2.1,2.3-3.3,4-2.2l25.8,16.8V77.4L49.1,56.9c-0.8-0.5-1.8-0.5-2.7,0l-9.8,5.7
	c-0.8,0.5-1.3,1.3-1.3,2.3v56.9c0,0.9,0.5,1.8,1.3,2.3l12.1,6.9C50.4,131.9,52.6,130.6,52.6,128.6z"
      />
    </svg>
    <!-- Content -->
    <article>
      <h1
        @input="direction_correct($event)"
        v-if="h1"
        contenteditable="true"
        spellcheck="false"
        :dir="direction"
      >
        I Am Groot !
      </h1>
      <p
        @input="direction_correct($event)"
        v-if="p1"
        contenteditable="true"
        spellcheck="false"
        :dir="direction"
      >
        We Are Groot
      </p>
      <code
        v-if="code"
        :style="`background: ${codebg}; color: ${codetext}`"
        contenteditable="true"
        spellcheck="false"
      ></code>
      <p
        @input="direction_correct($event)"
        v-if="p2"
        contenteditable="true"
        spellcheck="false"
        :dir="direction"
      >
        We Are Groot
      </p>
    </article>

    <!-- Add ons -->
    <div v-if="follow" class="follow">
      <i class="fab fa-telegram"></i>
      <i class="fab fa-instagram"></i>
      <b>@iammoneer</b>
    </div>
    <div v-if="follow" class="follow">
      <i class="fab fa-facebook"></i>
      <i class="fab fa-linkedin"></i>
      <b>Moneer Al-Dakkak</b>
    </div>
  </div>
</template>

<script>
export default {
  name: "post",
  props: [
    "bg",
    "text",
    "follow",
    "h1",
    "p1",
    "p2",
    "code",
    "codetext",
    "codebg",
  ],
  data() {
    return {
      en: /[a-zA-Z0-9]/,
      ar: /[\u0621-\u064A]/,
      direction: "ltr",
    };
  },
  methods: {
    direction_correct(e) {
      if (this.ar.test(e.currentTarget.textContent)) {
        this.direction = "rtl";
      } else {
        this.direction = "ltr";
      }
    },
  },
};
</script>

<style lang="scss" scoped>
.post {
  width: 480px;
  height: 480px;
  margin: 10px;
  display: inline-block;
  position: relative;
  color: white;
  padding: 20px;

  & * {
    outline: none;
  }

  ::selection {
    display: block;
    padding: 4px;
    border-radius: 7px;
    background: #3a67c9;
    color: white;
  }

  code {
    display: block;
    width: 100%;
    padding: 20px;
    border-radius: 6px;
    font-size: 1.2em;
    color: white;
  }

  article {
    position: absolute;
    width: calc(100% - 40px);
    top: 50%;
    left: 20px;
    transform: translateY(-55%);

    h1 {
      font-size: 2rem;
    }

    p {
      font-size: 1.16em;
    }
  }

  svg {
    width: 50px;
    height: 50px;
    position: absolute;
    top: 16px;
    left: 16px;
  }

  .follow {
    position: absolute;
    bottom: 40px;
    left: 20px;
    font-size: 0.8em;

    &:last-of-type {
      position: absolute;
      bottom: 20px;
      left: 20px;
    }

    i:last-of-type {
      border-right: 2px solid white;
      padding-right: 4px;
      margin: 0 4px;
    }
  }
}
</style>
